* {
	box-sizing: border-box;
  }
  
  html {
	font-family: "Fira Sans", sans-serif;
	font-size: 16px;
	font-weight: normal;
	line-height: 1.5;
	scroll-behavior: smooth;
  }
  
    ul,
    li,
    button,
    input,
    textarea,
    span,
    p {
      margin: 0;
      padding: 0;
      outline: none;
    }
  
  body {
    max-width: 1360px;
    margin-left: auto;
    margin-right: auto;
    background-color: #E5E5E5;
  }

  .container {
    position: relative;
    overflow: hidden;
  
    @media (min-width: 1360px) {
      box-shadow: 0 0 65px 0 rgba(0,0,0,0.2);
      margin: 0 auto;
    }

    @media (max-width: 768px) {
      overflow-y: visible;
    }
  }
  
    h1,
    h2,
    h3,
    h4 {
      margin: 0;
    }
    
  h1 {
	font-family: Fira Sans;
	font-size: 80px;
	font-style: normal;
	font-weight: 700;
	line-height: 70px;
	letter-spacing: -1px;
	text-align: left;
	height: 140px;
    width: 474px;
    left: 126px;
    top: 232px;
    border-radius: nullpx;
  }

a {
	text-decoration: none;
  }
  
  button:focus,
  input:focus {
	outline: none;
  }

.header {
  position: relative;
  // width: 1360px;

}
.header__container {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    top: 85px;
    margin-left: 119px;
    margin-right: 101px;

    @media (max-width: 1024px) { 
      margin-left: 50px;
    }

    @media (max-width: 420px) { 
      margin-right: 50px;
    }
}

.images {
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 1;

  &__searcher {
    margin-left: 250px;

    @media (max-width: 1024px) { 
      margin-left: 180px;
    }

    @media (max-width: 768px) { 
      margin-left: 120px;
    }

    @media (max-width: 420px) { 
      margin-left: 40px;
    }
  }
  @media (max-width: 1024px) { 
    max-width: 256px;
  } 
}

.header__label {
  z-index: 4;
  width: 60px;
  height: 29px;
  margin-top: -29px;
  cursor: pointer;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url("./images/Menu.svg");
  display: block;
}


 .header__toggler {
  display: none;
  z-index: 4;

   &:checked + .header__label {
    background-image: url("./images/cross.png");
    z-index: 4;
  }

   &:checked ~ .burger {
    right: 0;
  }
}

.burger {
  display: none;
  position: absolute;
  top: -85px;
  right: 100vw;
  height: 173vh;
  z-index: 3;
  width: 111%;
  //styleName: Hero;
  font-family: Fira Sans;
  font-size: 60px;
  font-style: normal;
  font-weight: 700;
  line-height: 70px;
  letter-spacing: -1px;
  text-align: left;
  opacity: 90%;

  background-color: #1434E1;

   transition: right 2s;
    display: block;

  &__list {
    list-style-type: none;
    margin-top: 267px;
  }

   &__item {
    margin-left: 255px;
    margin-bottom: 21px;
    // margin-bottom: 50px;
    // border-bottom: 1px solid white;
  }

   &__link {
    color: white;
    display: block;
    margin-bottom: 10px;
  }
}

.header__background { 
	position: absolute;
	width: 100%;
	height: 900px;
	left: 6px;
	top: -3px;

  background-image: url("./images//BG.png");

  @media (max-width: 420px) { 
    left: 0;
   }
}


